import React, { Component } from 'react'
import Navbar from 'react-bootstrap/Navbar';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { AiFillBug } from "react-icons/ai";
import { AiFillHome } from "react-icons/ai";
import { AiOutlineLink } from "react-icons/ai";


export default class MyNav extends Component {
  render() {
    return (
      
         
        <Navbar variant="light" expand="md" >
        <Container>
          <Navbar.Brand href="#home"><AiFillBug style={{ marginRight: '15px', fontSize: '40px' }} /></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="#home"><AiFillHome style={{ marginBottom: '7px', fontSize: '30px' }} /> Home</Nav.Link>
              <Nav.Link href="#link"><AiOutlineLink style={{ marginBottom: '7px', fontSize: '30px' }} />Link</Nav.Link>
      
              <NavDropdown title="Products" id="basic-nav-dropdown" style={{ marginTop: '5px' }}>
                <NavDropdown.Item href="#Products/1">productd 1</NavDropdown.Item>
                <NavDropdown.Item href="#Products/2">
                  productd 2</NavDropdown.Item>
      
                <NavDropdown.Item href="#Products/3">productd 3</NavDropdown.Item>
      
      
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
        );
  }
}
